.twoFactorPopup-modal {
    .b-popup {
        &-header {
            &--select {
                list-style-type: none;
                text-align: center;
                margin-bottom: 0;
                padding-left: 0;

                & > li {
                    display: inline-block;
                    margin: 0 10px;
                    width: 50px;
                    height: 50px;
                    cursor: pointer;

                    background: {
                        position: 50% 50%;
                        repeat: no-repeat;
                        size: cover;
                    }

                    &.google {
                        background-image: url("/static/img/google-auth.png");
                    }
                }
            }
        }

        &-submit {
            &--signUpCode {
                margin-top: 40px;

                .title {
                    margin-bottom: .5rem;
                }

                .info {
                    background: $color_17;
                    border-radius: 4px;
                    color: $color_1;
                    padding: {
                        top: 18px;
                        bottom: 14px;
                        left: 14px;
                    }

                    @include font_subheading_section;
                }

                .copy {
                    height: 49px;
                    margin-top: 24px;
                    width: auto;
                    min-width: auto;
                    color: $color_30;
                    background: transparent;
                    border-radius: 4px;
                    mix-blend-mode: normal;
                    border: 1px solid $color_30;
                    box-sizing: border-box;
                    box-shadow: 0px 2px 6px rgba(18, 13, 255, 0.1);

                    &:focus {
                        outline: none;
                        box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25);
                    }
                }
            }

            &--code {
                margin-top: 20px;
            }

            &--qrcode {
                width: 148px;
            }
        }
    }
}