.tryout-wrap {
	position: relative;

	.b-tryitout {
		&--left {
			padding-left: 90px;
		}
	}
}

.b-tryitout {
	&--left {

		&_title {
			color: $white;

			@include font_heading_big;
		}

		&_description {
			color: $white;
			padding-right: 20%;

			@include font_description_main;
		}

		&_reference {
			margin-top: 50px;

			&_content {
				.internal {
					line-height: 17px;

					@include font_subheading;
				}

				a {
					color: $white;
					text-decoration: none;
					mix-blend-mode: normal;
					border: 1px solid $white;
					box-sizing: border-box;
					border-radius: 18px;
					padding: 15px 25px 15px;

					&:hover {
						text-decoration: none;
					}
				}
			}
		}
	}

	&-services {
		width: auto;
		margin: 0 auto;
		mix-blend-mode: normal;

		&--tabs {
			background: $color_9;
			backdrop-filter: blur(10px);
			border-top-left-radius: 4px;
			border-top-right-radius: 4px;
			padding-top: 15px;

			ul {
				display: flex;
				justify-content: center;
				list-style: none;
				padding-left: 0;
				margin-bottom: 0;

				li {
					display: inline-block;
					cursor: pointer;
					color: $white;
					opacity: 0.4;
					padding-left: 30px;
					padding-right: 30px;
					padding-bottom: 15px;

					@include font_subheading_section_big;

					&.active {
						opacity: 1;
					}

					&:first-child {
						margin-left: 10px;
					}

					&:last-child {
						margin-right: 10px;
					}
				}
			}
		}

		&--services {
			background: $white;
			backdrop-filter: blur(10px);
			border-bottom-left-radius: 4px;
			border-bottom-right-radius: 4px;
			padding: 40px;

			&.exchange {
				padding-bottom: 20px;
			}

			.b-service--exchange {
				.b-service {
					&-from {
						&-select {
							width: 110px;
						}

						&-input {
							width: calc(100% - 7% - 110px);
						}
					}

					&-to {
						&-select {
							width: 110px;
						}

						&-input {
							width: calc(100% - 7% - 110px);
						}
					}
				}
			}
		}
	}

	&--left, &--right {
		display: inline-block;
		width: 50%;
		height: 100%;
		vertical-align: top;
		padding: 20px;
	}
}