.b-service {
    &--exchange {
        input {
            transition: border-color 1.5s ease-in-out,
                color 1.5s ease-in-out;
        }

        .b-service-from-list-top, .b-service-to-list-top {
            max-width: 120px;
        }

        .form-control {
            @include font_subheading_section_big;
            padding: {
                top: .675rem;
                bottom: .675rem;
            }
        }

        .b-service-list-top {
            height: 49px;
            margin-bottom: 10px;
            padding-top: 15px;
            margin-top: 5px;

            @include font_tabs;

            font-weight: $font-weight-body;

            .icon {
                font-size: 13px;
            }
        }

        .b-service-post {
            margin-top: 25px;
        }
    }

    &-from, &-to {
         vertical-align: top;

        & > div {
            display: inline-block;
        }
    }

    &-from, &-to, &-mid {
        display: inline-block;
    }

    &-from {
        width: 42%;

        &-select {
            margin-right: 7%;
            width: 110px;
            vertical-align: top;
        }

        &-input {
            vertical-align: 4px;
            width: calc(100% - 7% - 110px);
        }
    }

    &-to {
        width: 42%;

        &-select {
            margin-left: 7%;
            width: 110px;
            vertical-align: top;
        }

        &-input {
            vertical-align: 4px;
            width: calc(100% - 7% - 110px);
        }
    }

    &-mid {
        width: 16%;
        vertical-align: top;
        text-align: center;
        margin-top: 29px;

        &--switch, &--rateInfo {
            display: inline-block;
        }

        &--rateInfo {
            color: $color_23;
            vertical-align: top;
            margin-top: 25px;
            margin-left: 16px;

            @include font_subheading_section_big;

            font-weight: $font-weight-body;

            .equals {
                margin: 0 5px;
            }

            .numFrom, .numTo {
                margin-right: 3px;
            }
        }

        &--switch {
            transition: box-shadow .2s ease-in-out;
            position: relative;
            font-size: 14px;
            width: 84px;
            height: 37px;
            background: $color_3;
            mix-blend-mode: none;
            box-shadow: 0px 2px 6px rgba(18, 13, 255, 0.1);
            border-radius: 4px;
            cursor: pointer;

            &.disabled {
                cursor: default;
                pointer-events: none;
                opacity: 0.4;
            }

            .left-arrow, .right-arrow {
                position: absolute;
                color: $white;
            }

            .left-arrow {
                @include icon(before, left-arrow);
                top: calc(17px - 11px);
                left: calc(42px - 11px);
            }

            .right-arrow {
                @include icon(before, right-arrow);
                bottom: calc(17px - 13px);
                right: calc(42px - 13px);
            }

            &:hover {
                box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25);
            }
        }

        .form-group {
            margin-bottom: 0;
        }
    }
}

.rateEnd {
    input:not(.btn) {
        border-color: $color_25;
        color: $color_25;
    }
}

.invalid-feedback {
    &.rate {
        opacity: 0;
        margin-top: 5px;
        transition: opacity 1.5s ease-in-out;
        color: $color_25;

        @include font_subheading_section;
    }

    &.rateEnd {
        opacity: 1;
	}
}