.b-how-it-works {
	position: relative;
	padding: 70px 20px 65px 90px;
	background: $color_14;

	&--title {
		color: $color_9;

		@include font_heading_section;
	}

	&-content {
		margin: 40px auto 10px;

		&-inner {
			display: flex;
			flex-flow: column wrap;
			width: auto;
			height: 735px;
			padding: 5px;

			&--left {
				margin: {
					bottom: auto;
					top: auto;
				}
			}

			.transitionMain {
				transform: scale(1.05);
			}

			.transitionSecondary {
				transform: scale(1.1);
			}

			.main {
				flex: 1 0 100%;
				margin-right: 5px;
				width: 1043px;
				height: 650px;

				img {
					height: 650px;
				}

				.how-to-image--hover-title {
					color: $color_9;
					top: 50%;

					@include font_heading_section;
				}
			}

			.secondary {
				flex: 1;
				width: 214px;
				height: 132px;
				margin-bottom: 1rem;

				img {
					-webkit-filter: blur(2px);
					filter: blur(2px);
				}

				.how-to-image--hover-title {
					color: $color_1;
					top: 45%;

					@include font_subheading_section;
					font-weight: $font-weight-display;
				}
			}

			.main, .secondary {
				cursor: pointer;
				transition: transform .2s;

				figure {
					position: relative;
					margin-bottom: 0;
				}

				img {
					box-shadow: 0px 4px 16px rgba(94, 165, 255, 0.45);
				}

				.how-to-image--hover-title {
					width: 100%;
					text-align: center;
					position: absolute;
					left: 0;
				}
			}
		}
	}
}