.l-header-entrance {
    background: $white;
    max-height: 100px;

    &-block {
        margin: 0 20px 0;

        @include clearfix;
    }

    &-logo {
        width: 20%;
        float: left;
        margin: 1px 0 14px;

        @include clearfix;

        & > a {
            display: inline-block;
            height: auto;
            text-decoration: none;
            color: inherit;
        }

        &_image {
            width: 90px;
            height: 80px;
            background: {
                image: url("/static/img/logo-internal.png");
                position: 50% 50%;
                repeat: no-repeat;
                size: cover;
            }
        }

        &_title {
            color: $color_1;
            vertical-align: 18px;

            @include font_logo;
        }

        &_image, &_title {
            display: inline-block;
        }
    }

    &-content {
        width: auto;
        margin: 0 auto;
        float: right;
        padding-right: 35px;
        margin-top: 30px;

        a, a:hover {
            color: inherit;
            text-decoration: none;
        }

        & > a {
            display: inline-block;
            vertical-align: middle;
        }
    }

    &-language-selector {
        display: inline-block;
        vertical-align: baseline;
        margin-left: 40px;
    }

    &-reference {
        color: $color_30;
        mix-blend-mode: normal;
        border: 1px solid $color_30;
        box-sizing: border-box;
        box-shadow: 0px 2px 6px rgba(18, 13, 255, 0.1);
        border-radius: 4px;
        padding: 12px 21px 9px 19px;
        transition: box-shadow .2s ease-in-out;

        @include font_subheading_section;

        &:hover {
            box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
        }
    }
}