.l-dashboard {
	padding: {
		left: 10%;
		right: 10%;
		bottom: 100px;
		top: 20px;
	}

	.b-dashboard {
		&-wrapper {
			display: flex;
			flex-wrap: wrap;
			flex-direction: row;
			height: 50vh;
		}

		&-service {
			display: flex;
			position: relative;
			flex-basis: calc(50% - 40px);
			justify-content: center;
			flex-direction: column;
			margin: 20px;
			background: $white;
			border-radius: 4px;
			transition: all .2s ease-in-out;
			overflow: hidden;

			& > a {
				display: flex;
				flex: 1;
				flex-direction: column;
				text-decoration: none;
				color: $color_29;

				@include font_dashboard;
			}

			&:hover {
				background: $color_28;
				mix-blend-mode: normal;
				box-shadow: 0px 4px 6px rgba(36, 123, 233, 0.1);

				.b-dashboard-service-icon {
					.image {
						&.withdraw {
							@include withdraw_element(186px, 140px, $color_29, $color_28, $arr-padding: 20, $top: -159px, $left: -22px, $border-width: 2px, $border-radius: 10px, $border-arrow-width: 4px, $transition: true);
						}
					}
				}
			}

			&-title {
				position: absolute;
				bottom: 30px;
				left: 50px;
			}

			&-icon {
				.image {
					position: absolute;
					top: -6px;
					right: -70px;

					&.exchange {
						@include exchange_element(213px, 160px, $color_29, transparent, 2px, 12px, $font-size: 87px, $top-offset: 24px, $side-offset: 70px);
					}

					&.deposit {
						top: 14px;
						right: -24px;

						@include deposit_element(186px, 140px, $color_29, transparent, 2px, 10px, $font-size: 108px, $top: 19px, $left: 39px);
					}

					&.withdraw {
						width: 186px;
						height: 140px;
						top: 15px;
						right: -51px;

						@include withdraw_element(186px, 140px, $color_29, $white, $arr-padding: 20, $top: -159px, $left: -22px, $border-width: 2px, $border-radius: 10px, $border-arrow-width: 4px, $transition: true);
					}

					&.mobile-topup {
						top: -3px;
						right: -50px;

						@include topup_element(209px, 156px, $color_29, $border-width: 2px, $border-radius: 10px, $arr-padding: 21px);
					}
				}
			}
		}
	}
}