.bs-modal_fix {
    display: table;
    width: 100%;
    height: 100%;
    padding: 0 20px;
    pointer-events: none;

    &-helper {
        display: table-cell;
        pointer-events: none;
        vertical-align: middle;
    }
}

.modal {
    &-content {
        width: inherit;
        max-width: inherit;
        height: inherit;
        margin: 0 auto;
        pointer-events: all;
        background: $white;
        mix-blend-mode: normal;
        border-radius: 4px;

        @include from-tablet {
            width: 480px;
            max-width: 100%;
        }
    }

    &-header {
        border-bottom: none;
        cursor: default;
        padding: 18px 24px;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
        color: $color_1;
        background: $color_28;
        mix-blend-mode: normal;
        box-shadow: 0px 4px 6px rgba(36, 123, 233, 0.1);

        @include font_heading_footer;
    }

    &-body {
        padding: 40px;
        border-bottom-left-radius: 4px;
        border-bottom-left-radius: 4px;
        box-shadow: 0px 0px 6px rgba(36, 123, 233, 0.1);

        .btn {
            height: 52px;
            min-width: 120px;
        }
    }
}

.b-popup {
    &-close {
        cursor: pointer;
        font-size: 15px;
    }

    &-submit {
        &--submit {
            margin-top: 15px;
        }

        &--title {
            text-align: center;
            color: $color_1;
            margin-bottom: 15px;

            @include font_heading_footer;
        }

        &--form {
            &-content {
                color: $color_1;
                
                @include font_subheading_section;

                .general-list-item {
                    margin-bottom: 6px;
                    margin-top: 6px;

                    .name {
                        margin-right: 7px;
                    }

                    .value {
                        font-weight: $font-weight-display;
                    }

                    &:first-child {
                        margin-top: 0;
                    }

                    &:last-child {
                        margin-bottom: 15px;
                    }
                }
            }

            &-refresh {
                .invalid-feedback {
                    &.rate {
                        margin-top: 7px;
                        font-weight: $font-weight-display;
                    }
                }
            }
        }
    }

    &-text {
        &-text {
            text-align: center;
            color: $color_1;
            margin-bottom: 25px;

            @include font_subheading_section;
        }
    }

    &-success {
        color: $color_1;
        text-align: center;

        @include font_subheading_section;
    }

    &--confirm-exchange {
        .modal {
            &-body {
                padding-bottom: 20px;
            }
        }
    }

    &.success {
        .modal {
            &-content {
                background: $color_33;
            }

            &-header {
                background: $color_34;
            }
        }
    }

    &.fail {
        .modal {
            &-content {
                background: $color_35;
            }

            &-header {
                background: $color_36;
            }
        }
    }

    &.pending {
        .modal {
            &-content {
                background: $color_37;
            }

            &-header {
                background: $color_38;
            }
        }
    }

    &.success, &.fail, &.pending {
        .modal {
            &-content {
                box-shadow: 0px 4px 6px rgba(36, 123, 233, 0.1);
            }

            &-header {
                box-shadow: none;
            }

            &-body {
                box-shadow: 0px 4px 10px rgba(36, 123, 233, 0.2);

                .btn {
                    background: transparent;
                    box-shadow: 0px 2px 6px rgba(18, 13, 255, 0.1);
                    color: $color_1;
                    mix-blend-mode: normal;
                    box-sizing: border-box;
                    border: 1px solid $color_1;
                }
            }
        }
    }
}