.b-middle-row {
    &.login, &.registration, &.profile {
        min-height: calc(100vh - 100px);
        position: relative;

        .b-main {
            width: 100%;

            &-login-resend-registration {
                margin-bottom: 5px;
            }

            &-register-resend-registration,
            &-login-forgot-password,
            &-login-resend-registration {
                color: $color_1;
                width: fit-content;
                cursor: pointer;
                text-decoration: underline;
                transition: color .1s ease-in-out;

                @include font_history;

                &:hover {
                    color: $black;
                }
            }

            &-block {
                flex-grow: 1;
                flex-shrink: 0;
                min-height: calc(100vh - 100px - 30px);
                padding: 70px 30% 0;

                @include desktop-all {
					min-height: calc(100vh - 100px - 30px);
				}

				@include desktop-hd {
					min-height: calc(100vh - 100px - 104px);
				}

                &-form {
                    background: $white;
                    mix-blend-mode: normal;
                    border-radius: 4px;

                    &-form {
                        .btn {
                            height: 52px;
                            min-width: 140px;

                            &.change_password-button {
                                min-width: auto;
                                height: 52px;
                                margin-top: 24px;
                                color: $color_30;
                                background: transparent;
                                border-radius: 4px;
                                mix-blend-mode: normal;
                                border: 1px solid $color_30;
                                box-sizing: border-box;
                                box-shadow: 0px 2px 6px rgba(18, 13, 255, 0.1);
                                transition: box-shadow .2s ease-in-out;

                                &:hover {
                                    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
                                }
                            }
                        }
                    }

                    &-general {
                        padding: 40px;
                        border-bottom-left-radius: 4px;
                        border-bottom-right-radius: 4px;
                        box-shadow: 0px 0px 6px rgba(36, 123, 233, 0.1);
                    }
                }

                &-title {
                    cursor: default;
                    padding: 18px 24px;
                    border-top-left-radius: 4px;
                    border-top-right-radius: 4px;
                    color: $color_1;
                    background: $color_28;
                    mix-blend-mode: normal;
                    box-shadow: 0px 4px 6px rgba(36, 123, 233, 0.1);

                    @include font_heading_footer;
                }
            }

            &-footer {
                flex-shrink: 0;
                width: 100%;
                height: 30px;
                background: $color_26;

                @include desktop-all {
					height: 30px;
				}

				@include desktop-hd {
					height: 104px;
				}
            }
        }
    }
}