.lightbox {
	&-content-center {
		width: 100%;
		height: 100%;
		position: absolute;
	}

	&-modifiers-box {
		background-color: rgba(0, 0, 0, 0.2);
		text-align: center;
		font-size: 1.2em;
		position: absolute;
		width: 100%;
		z-index: 1;
	}

	&-modifiers-box {
		@media (min-height: 600px) {
			background-color: transparent;	
		}
	}

	&-image-container {
		overflow: hidden;
		height: calc(100% - 102px);
		margin-top: 40px;
		margin: 0 5em;

		@include mobile {
			margin: 0;
		}
	}

	&-image {
		height: 100%;
		background-repeat: no-repeat;
	}

	&-loader {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
      	align-items: center;
        -ms-flex-pack: center;
        justify-content: center;
        height: 100%;
        fill: $white;
        -webkit-animation: rotating 2s linear infinite;
        animation: rotating 2s linear infinite;
	}
}

@keyframes rotation {
	from {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(-360deg);
		transform: rotate(-360deg);
	}
}