.l-footer-landing {
	background: {
		image: url("/static/img/landing-background-3600.png");
		position: 50% 60%;
		size: cover;
		repeat: no-repeat;
	}
	max-height: 105px;

	&--socials {
		margin-top: 30px;
		margin-left: 5%;

		@include font_heading_footer;

		a {
			color: $white;
			text-decoration: none;

			&:hover {
				opacity: 0.4;
			}
		}
	}

	&--mark {
		margin-bottom: 15px;
		color: $white;
		text-align: center;

		@include font_heading_footer;

		&-brand {
			.trademark {
				margin-right: 2px;
			}

			.brand-name {
				margin-left: 2px;
			}
		}

		&-timeframe {
			margin-left: 5px;

			.mid {
				margin-left: 2px;
				margin-right: 2px;
			}
		}

		&-brand, &-timeframe {
			display: inline-block;
		}
	}
}