.b-main-account {
    &-body {
        .account-title {
            color: $color_1;
            position: absolute;
            top: 50%;

            @include font_subheading_section;
        }

        &--password, &--twofactor {
            position: relative;
        }

        &--email {
            .row > div {
                padding-right: 33px;

                .title {
                    margin-left: 10px;
                    margin-bottom: 5px;
                }
            }

            .info-field {
                background-color: $color_17;
                border-radius: 4px;
                color: $color_1;
                padding: {
                    top: 18px;
                    bottom: 14px;
                    left: 14px;
                }

                @include font_subheading_section;
            }
        }

        &--twofactor {
            margin-top: 30px;

            .account-title {
                top: 0;
            }

            .value {
                .switch {
                    position: relative;
                    display: inline-block;
                    width: 45px;
                    height: 26px;

                    input {
                        display: none;

                        &.primary:checked + .slider {
                            background-color: #2196F3;
                        }

                        &:focus + .slider {
                            box-shadow: 0 0 1px #2196F3;
                        }

                        &:checked + .slider:before {
                            -webkit-transform: translateX(20px);
                            -ms-transform: translateX(20px);
                            transform: translateX(20px);
                        }
                    }

                    .slider {
                        position: absolute;
                        cursor: pointer;
                        top: 0;
                        left: 0;
                        right: 0;
                        bottom: 0;
                        background-color: #ccc;
                        -webkit-transition: .4s;
                        transition: .4s;

                        &.round {
                            border-radius: 24px;

                            &:before {
                                border-radius: 50%;
                            }
                        }

                        &:before {
                            position: absolute;
                            content: "";
                            height: 24px;
                            width: 24px;
                            left: 0.5px;
                            bottom: 1px;
                            background-color: $white;
                            -webkit-transition: .4s;
                            transition: .4s;
                        }
                    }
                }
            }
        }
    }
}