.l-header-landing {
	height: 130px;
	width: 100%;
	background: transparent;
	position: relative;

	&-block {
		height: 120px;
		width: auto;
		margin: 0 20px 0;
		padding-left: 40px;
		padding-right: 40px;
		justify-content: space-between;

		@include clearfix;
	}

	&-logo {
		margin-left: 40px;
		padding-top: 4px;
		color: $black;
		text-decoration: none;
		width: auto;

		a {
			color: $white;
			width: 88px;
			height: auto;
			display: inline-block;

			&:hover {
				text-decoration: none;
			}
		}

		&_image {
			display: inline-block;
			width: 90px;
			height: 80px;
			background: {
				image: url("/static/img/logo.png");
				position: 50% 50%;
				repeat: no-repeat;
				size: cover;
			}
		}

		&_title {
			display: inline-block;
			color: $white;
			opacity: 0.4;
			vertical-align: 18px;

			@include font_logo;
		}
	}

	&-menu {
		width: calc(100% - 262px);
		margin: 0 auto;

		&--left {
			padding-left: 35px;

			ul {
				margin: 0;
				padding: 0;
				list-style: none;

				li {
					display: inline-block;
					margin: 0 20px;

					@include font_heading;

					a {
						text-decoration: none;
						color: $white;
						opacity: 0.4;

						&:hover {
							text-decoration: none;
							opacity: 1;
						}
					}
				}
			}
		}

		&--right {
			padding-right: 10px;
			float: right;
		}

		&--left, &--right {
			display: inline-block;
			vertical-align: 18px;
		}

		&_icon {
			position: relative;
			display: inline-block;
			color: $white;
			opacity: 0.4;

			&:hover {
				opacity: 1;
			}

			@include icon(before, gear);

			&:hover .l-header_menu-loginlist {
				display: block;
			}

			.l-header_menu-loginlist {
				display: none;
				position: absolute;
				width: 150px;
				top: 23px;
				right: 0;
				padding: 0;
				text-align: left;
				z-index: 2;
				border-radius: 3px;
				background: rgba(241, 248, 254, 0.5);
				background-clip: padding-box;
				border: 1px solid rgba(0, 0, 0, .15);
				box-shadow: 0 3px 8px rgba(0, 0, 0, .3);

				li {
					list-style: none;
					display: block;
					padding: 6px;
					text-decoration: none;
					color: $color_1;

					@include font_heading;

					&:hover {
						text-decoration: underline;
					}

					a {
						text-decoration: none;
						color: $color_1;

						&:hover {
							text-decoration: none;
						}
					}
				}
			}
		}
	}

	&-language-selector {
		padding: 0 25px 0 0;
	}



	&-logo, &-menu {
		display: inline-block;
	}

	&-language-selector, &-user, &-infoblock {
		display: inline-block;
	}

	&-reference {
		.login, .register {
			display: inline-block;

			@include font_heading;

			a {
				color: $white;
				opacity: 0.4;
				text-decoration: none;

				&:hover {
					opacity: 1;
					text-decoration: none;
				}
			}
		}

		.login {
			padding: 0 25px 0;
		}

		.register {
			padding-left: 25px;
		}

		&.l-header-landing-app {
			display: inline-block;
			padding: 0 25px 0 0;

			@include font_heading;

			a {
				color: $white;
				opacity: 0.4;
				text-decoration: none;

				&:hover {
					opacity: 1;
					text-decoration: none;
				}
			}
		}
	}

	&-username {
		display: inline-block;
		padding: 0 5px 0 0;
		color: white;

		@include font_heading;
	}

	&.logged {
		.l-header-landing {
			&-block {
				margin: 0 5px 0;
				padding-left: 5px;
				padding-right: 5px;

				@include desktop-all {
					margin: 0 5px 0;
					padding-left: 5px;
					padding-right: 5px;
				}

				@include desktop-hd {
					margin: 0 20px 0;
					padding-left: 40px;
					padding-right: 40px;
				}
			}

			&-language-selector {
				padding: 0 5px 0 0;

				@include desktop-all {
					padding: 0 5px 0 0;
				}

				@include desktop-hd {
					padding: 0 25px 0 0;
				}
			}
		}
	}
}