.lightbox {
	&-open {
		overflow: hidden;
		position: fixed;
		width: 100%
	}

	&-backdrop {
		position: fixed;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.8);
		top: 0;
		left: 0;
		z-index: 100;
	}

	&-btn-left,
	&-btn-right,
	&-btn-close,
	&-title-content,
	&-modifiers-box {
		-webkit-transition: opacity 200ms ease;
		-o-transition: opacity 200ms ease;
		transition: opacity 200ms ease;
	}

	&-btn-close {
		position: fixed;
		left: 0;
		z-index: 2;
	}

	&-btn-right {
		position: absolute;
		top: 50%;
		margin-top: -1.5em;
		right: 0;

		@include from-tablet {
			padding-right: 10px;
		}
	}

	&-btn-left {
		position: absolute;
		top: 50%;
		margin-top: -1.5em;
		left: 0;

		@include from-tablet {
			padding-left: 10px;
		}
	}

	&-title-content {
		background-color: rgba(0, 0, 0, 0.2);
		-webkit-box-shadow: 0 5px 5px -3px rgba(0,0,0,.2), 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0, 0, 0, .12);
        box-shadow: 0 5px 5px -3px rgba(0,0,0,.2), 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0, 0, 0, .12);
        position: absolute;
        width: 100%;
        z-index: 1;
        bottom: 0;
        text-align: center;
        padding: 10px;

        @include font_subheading_section;
	}

	&-title {
		color: $white;
	}

	&-description {
		color: $color_16;
	}

	&-transition-image-appear {
		opacity: 0.1;
	}

	&-transition-image-appear.lightbox-transition-image-appear-active {
		opacity: 1;
		-webkit-transition: opacity .3s ease-in;
		-o-transition: opacity .3s ease-in;
		transition: opacity .3s ease-in;
	}

	&-transition-image-enter-right {
		-webkit-transform: translate(100%);
		-ms-transform: translate(100%);
		transform: translate(100%);
	}

	&-transition-image-enter-right.lightbox-transition-image-enter-right-active {
		-webkit-transform: translate(0%);
		-ms-transform: translate(0%);
		transform: translate(0%);
		-webkit-transition: -webkit-transform 300ms ease-in-out;
		transition: -webkit-transform 300ms ease-in-out;
		-o-transition: transform 300ms ease-in-out;
		transition: transform 300ms ease-in-out;
		transition: transform 300ms ease-in-out, -webkit-transform 300ms ease-in-out;
	}

	&-transition-image-leave-right {
		-webkit-transform: translate(0%);
		-ms-transform: translate(0%);
		transform: translate(0%);
	}

	&-transition-image-leave-right.lightbox-transition-image-leave-right-active {
		-webkit-transform: translate(-100%);
		-ms-transform: translate(-100%);
		transform: translate(-100%);
		-webkit-transition: -webkit-transform 300ms ease-in-out;
		transition: -webkit-transform 300ms ease-in-out;
		-o-transition: transform 300ms ease-in-out;
		transition: transform 300ms ease-in-out;
		transition: transform 300ms ease-in-out, -webkit-transform 300ms ease-in-out;
	}

	&-transition-image-enter-left {
        -webkit-transform: translate(-100%);
        -ms-transform: translate(-100%);
        transform: translate(-100%);
	}

	&-transition-image-enter-left.lightbox-transition-image-enter-left-active {
		-webkit-transform: translate(0%);
		-ms-transform: translate(0%);
        transform: translate(0%);
        -webkit-transition: -webkit-transform 300ms ease-in-out;
        transition: -webkit-transform 300ms ease-in-out;
        -o-transition: transform 300ms ease-in-out;
        transition: transform 300ms ease-in-out;
  		transition: transform 300ms ease-in-out, -webkit-transform 300ms ease-in-out;
	}

	&-transition-image-leave-left {
		-webkit-transform: translate(0%);
        -ms-transform: translate(0%);
        transform: translate(0%);
	}

	&-transition-image-leave-left.lightbox-transition-image-leave-left-active {
		-webkit-transform: translate(100%);
        -ms-transform: translate(100%);
        transform: translate(100%);
        -webkit-transition: -webkit-transform 300ms ease-in-out;
  		transition: -webkit-transform 300ms ease-in-out;
        -o-transition: transform 300ms ease-in-out;
  		transition: transform 300ms ease-in-out;
  		transition: transform 300ms ease-in-out, -webkit-transform 300ms ease-in-out;
	}
}

.hide-controls .lightbox-btn-left,
.hide-controls .lightbox-btn-right {
	@include mobile {
		opacity: 0;
		pointer-events: none;
	}
}