.b-lang-select {
    &-top {
        list-style-type: none;
        padding-left: 0;
        position: relative;
        color: $color_1;
        min-width: 140px;
        padding: .375rem 0.2rem;

        &.landing {
            padding: 0 0.2rem 0.375rem 0.2rem;
        }

        @include font_heading_footer;

        &:not(.empty) {
            cursor: pointer;
        }

        &:hover {
            .b-lang-select-bottom {
                visibility: visible;
                opacity: 1;
                transform: translateY(0%);
                z-index: 1;
				transition-delay: 0s, 0s, 0.3s;
            }

            .current {
                &--wrapper {
                    &-content {
                        &-opener {
                            &-relative {
                                &:before {
                                    transform: rotate(90deg);
                                }
                            }
                        }
                    }
                }
            }
        }

        .current {
            &--wrapper {
                &-content {
                    &-info, &-opener {
                        display: inline-block;
                    }

                    &-opener {
                        width: 34px;

                        &-relative {
                            text-align: center;
                            padding: 0 7px 0;

                            @include icon(before, arrow-right);

                            &:before {
                                color: $color_1;
                                display: inline-block;
                                font-size: 16px;
                                transition: 200ms linear transform;
                            }
                        }
                    }
                }
            }
        }
    }

    &-bottom {
        visibility: hidden;
        opacity: 0;
        transform: translateY(-0.5em);
        z-index: -1;
        list-style-type: none;
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 99;
        padding: 0;
        text-align: left;
        border-radius: 4px;
        background: $white;
        background-clip: padding-box;
        box-shadow: 0px 2px 10px rgba(0, 110, 255, 0.1);
        transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.3s, z-index 0s linear 0.01s;

        @include font_history;

        li {
            display: block;
            padding: 9px 20px;
            text-decoration: none;

            &:first-child {
                margin-top: 10px;
            }

            &:last-child {
                margin-bottom: 15px;
            }

            &:hover {
                background: $color_20;
            }
        }
    }
}

.l-header-landing {
    .b-lang-select {
        &-top {
            color: $white;

            &:hover {
                .b-lang-select-bottom {
                    opacity: 0.4;
                    transform: translateY(-5%);
                }

                .current {
                    &--wrapper {
                        &-content {
                            &-opener {
                                &-relative {
                                    &:before {
                                        transform: rotate(45deg);
                                    }
                                }
                            }
                        }
                    }
                }
            }

            .current {
                &--wrapper {
                    &-content {
                        &-opener {
                            width: 15px;

                            &-relative {
                                &:before {
                                    content: '';

                                    @include arrow(3px, 3px, $white);
                                }
                            }
                        }
                    }
                }
            }
        }

        &-bottom {
            background: transparent;
            box-shadow: none;

            @include font_heading_footer;

            li {
                padding: .375rem 0.2rem;

                &:first-child {
                    margin-top: 0;
                }

                &:hover {
                    background: transparent;
                }
            }
        }
    }
}