.rates-wrap {
	position: relative;

	.b-rates {
		&--left {
			padding-left: 90px;
		}
	}
}

.b-rates {
	&--left {
		&_title {
			color: $color_9;

			@include font_heading_section;
		}

		&_description {
			color: $color_1;

			@include font_description_main;
		}

		&_reference {
			margin-top: 125px;

			&_content {
				@include font_subheading_section;

				&_description {
					text-transform: uppercase;
					color: $color_9;
				}
			}

			&_external {
				padding-top: 10px;

				span {
					&:after {
						content: '/';
						margin: 0 7px 0;
					}

					&:last-child:after {
						content: '';
						margin: 0;
					}
				}

				a {
					color: $color_1;
					text-decoration: none;

					&:hover {
						color: $black;
						text-decoration: none;
					}
				}
			}
		}
	}

	&--right {
		padding-right: 50px;
	}

	&-chart {
		width: 700px;
		margin: 0 auto 0;
		mix-blend-mode: normal;
		box-shadow: 0px 2px 30px rgba(0, 0, 0, 0.1);
		border-radius: 4px;

		@include desktop-all {
			width: 700px;
		}

		@include desktop-hd {
			width: 810px;
		}

		&--tabs {
			margin-left: 20px;
			padding-top: 35px;

			ul {
				list-style: none;
				margin-bottom: 0;

				li {
					display: inline-block;
					cursor: pointer;
					padding-left: 5px;
					padding-right: 5px;
					padding-bottom: 10px;
					color: $color_11;
					border-bottom: 2px solid $color_11;

					@include font_tabs;

					&.active {
						color: $color_9;
						border-bottom: 2px solid $color_9;
					}

					.arrow {
						@include icon(before, arrow-right);

						&:before {
							font-size: 12px;
							vertical-align: top;
							padding-top: 2px;
							margin: 0 3px;
						}
					}
				}
			}
		}

		&--chart {
			padding-left: 40px;

			.outerRect {
				fill: $color_12;
			}

			.dot {
				fill: $color_9;
			}

			.tick text {
				fill: $color_1;

				@include font_axis;
			}

			.label {
				fill: $color_1;

				@include font_heading;
			}

			.area {
				fill: url(#areaGradient);
			}

			.line {
				stroke: $color_13;
				stroke-width: 3px;
			}

			.point-note {
				background: $white;

				.wrapper {
					width: 115px;
					height: 100%;
					padding: 5px;
					background-clip: padding-box;
					border: 1px solid rgba(0, 0, 0, .15);
					box-shadow: 0 3px 8px rgba(0, 0, 0, .3);
					color: $color_1;

					@include font_foreign_label;

					.date {
						margin-bottom: 5px;
					}

					.title {
						color: $black;
					}
				}
			}
		}
	}

	&--left, &--right {
		display: inline-block;
		width: 50%;
		height: 100%;
		vertical-align: top;
		padding: 70px 20px 20px;
	}
}