.b-history {
	$history: &;

	&-full, &-empty {
		background-color: $white;
		mix-blend-mode: normal;
		border-radius: 4px;

		&-title {
			cursor: default;
			padding: 18px 24px;
			border-top-left-radius: 4px;
			border-top-right-radius: 4px;
			color: $color_1;
			background: $color_28;
			mix-blend-mode: normal;
			box-shadow: 0px 4px 6px rgba(36, 123, 233, 0.1);

			@include font_heading_footer;
		}
	}

	&-empty {
		&-description {
			margin: 10px 25px;
			color: $color_1;

			@include font_subheading_section;
		}
	}

	&-item {
		border-radius: 4px;
		margin: 25px 15%;

		&:last-child {
			margin-bottom: 20px;
		}

		&:first-child {
			margin-top: 20px;
		}

		#{$history}_main-info, #{$history}-creation, #{$history}_additional-info {
			display: inline-block;
			vertical-align: middle;
		}

		#{$history}_main-info {

			& > div {
				@include clearfix;
			}

			.b-service {
				&-name, &-action {
					display: inline-block;
				}

				&-name {
					text-align: left;
					float: left;
				}

				&-action {
					text-align: right;
					float: right;

					&-mid {
						margin: 0 3px;
					}
				}
			}
		}

		#{$history}-creation {
			margin-left: 30px;
		}
	}
}

.l-history {
	display: flex;
	flex-direction: column;
	height: inherit;
	margin: 0 auto;
	padding: {
		left: 10%;
		right: 10%;
		top: 10px;
	}

	.b-history {
		&-item {
			cursor: pointer;
			margin: 15px 40px;
			background-color: $color_19;
			color: $color_21;
			padding: 8px 16px 4px;

			@include font_history;

			&--general {
				position: relative;
			}

			&.helperShow {
				cursor: default;
			}

			&:hover, &.helperShow {
				background: $color_9;
				color: $white;

				.b-service-left-wrapper .b-service-icon {
					.image {
						&.FiatWithdraw, &.CryptoWithdraw {
							@include withdraw_element(30px, 20px, $white, $color_9, $arr-padding: 4, $top: -25px);
						}

						&.Exchange {
							@include exchange_element(34px, 22px, $white, $color_9);
						}

						&.FiatDeposit, &.CryptoDeposit {
							@include deposit_element(34px, 22px, $white, $color_9, $font-size: 22px, $top: -5px, $left: 5px);
						}
					}
				}

				.b-service-status {
					&-wrapper {
						&--completed {
							color: $white;
						}

						&--expired, &--failed {
							color: $white;
						}
					}
				}
			}
		}

		&_main-info {
			width: 60%;
			margin-bottom: 0;
			margin-top: 0;
			position: relative;

			.b-service {
				&-left-wrapper .b-service-icon {
					.image {
						width: 34px;
						height: 22px;
						margin-bottom: 4px;

						&.FiatWithdraw, &.CryptoWithdraw {
							width: 30px;
							height: 20px;
							margin-left: 4px;
							@include withdraw_element(30px, 20px, $color_21, $color_19, $arr-padding: 4, $top: -25px);
						}

						&.Exchange {
							@include exchange_element(34px, 22px, $color_21, $color_19);
						}

						&.FiatDeposit, &.CryptoDeposit {
							@include deposit_element(34px, 22px, $color_21, $color_19, $font-size: 22px, $top: -5px, $left: 5px);
						}
					}
				}

				&-name {
					margin-left: 0;
				}
			}
		}

		&_additional-info {
			width: 40%;
			margin-bottom: 0;
			margin-top: 0;
			margin-left: 0;
			text-align: right;

			.b-service-status {
				&-wrapper {
					&--completed {
						color: $color_31;

						.b-service-status-icon {
							@include icon(before, approved-full);

							&:before {
								font-size: 17px;
							}
						}
					}

					&--expired, &--failed {
						color: $color_32;
					}

					&--expired {
						.b-service-status-icon {
							vertical-align: top;

							@include icon(before, exclamation-mark);

							&:before {
								font-size: 14px;
							}
						}
					}

					&--failed {
						.b-service-status-icon {
							vertical-align: top;

							@include icon(before, reject-new);

							&:before {
								font-size: 14px;
							}
						}
					}

					&--pending {
						.b-service-status-icon {
							vertical-align: top;

							@include icon(before, pending-full);

							&:before {
								font-size: 14px;
							}
						}
					}
				}

				&-name, &-icon {
					display: inline-block;
				}

				&-icon {
					margin-left: 9px;
					width: 15px;
					height: 15px;
				}
			}
		}

		&_action-info {
			position: absolute;
			text-align: center;
			width: 100%;
			left: 0;
			top: calc(50% - 10px);

			.b-service-action-text {
				.currency {
					@include font_history_small;
				}

				&-mid {
					@include icon(before, arrow-right);

					&:before {
						font-size: 9px;
						vertical-align: 1px;
						padding-top: 2px;
						margin: 0 3px;
					}
				}
			}
		}

		&_showmore-info {
			padding-top: 20px;

			.general-list {
				&-item {
					padding: 1px 0;

					.value {
						margin-left: 4px;
					}

					&:first-child {
						padding-top: 0;
					}

					&:last-child {
						padding-bottom: 0;;
					}

					&--showless {
						margin-top: 15px;
						margin-bottom: 5px;
						text-align: center;
						cursor: pointer;
					}
				}
			}
		}
	}
}