.l-header {
	background: $white;
	flex-basis: 100%;
	max-height: 100px;

	&-block {
		margin: 0 20px 0;

		@include clearfix;
	}

	&-logo {
		width: 20%;
		float: left;
		margin: 1px 0 14px;

		@include clearfix;

		& > a {
			display: inline-block;
			height: auto;
			text-decoration: none;
			color: inherit;
		}

		&_image {
			width: 90px;
			height: 80px;
			background: {
				image: url("/static/img/logo-internal.png");
				position: 50% 50%;
				repeat: no-repeat;
				size: cover;
			}
		}

		&_title {
			color: $color_1;
			vertical-align: 18px;

			@include font_logo;
		}

		&_image, &_title {
			display: inline-block;
		}
	}

	&_menu {
		$l_header_menu: &;

		width: auto;
		margin: 0 auto;

		&-loginlist {
			display: none;

			li {
				cursor: pointer;
			}
		}

		&_icon {
			&:hover {
				#{$l_header_menu}-loginlist {
					display: block;
				}
			}
		}

		&--left, &--right {
			display: inline-block;
		}

		&--left {
			padding-left: 70px;

			&_block {
				margin-top: 43px;
			}

			&_list_top, &_breadcrumb-block {
				display: inline-block;
			}

			&_list_top {
				margin: 0;
				padding: 0;
				list-style: none;

				@include clearfix;

				#{$l_header_menu} {
					&-item {
						float: left;
						position: relative;

						&:hover .l-header_menu--left_list_bottom {
							visibility: visible;
							opacity: 1;
							transform: translateY(0%);
							transition-delay: 0s, 0s, 0.3s;
						}

						> span {
							display: block;
							padding: 18px 15px;
							color: $color3;
							background-color: $color4;
						}
					}
				}

				&.active {
					.l-header_menu--left_list_bottom {
						visibility: visible;
						opacity: 1;
						transform: translateY(0%);
						transition-delay: 0s, 0s, 0.3s;
					}
				}
			}

			&_list_bottom {
				visibility: hidden;
				opacity: 0;
				transform: translateY(-2em);
  				z-index: 1;
				position: absolute;
				width: 150px;
				top: 30px;
				left: 0;
				padding: 0;
				text-align: left;
				border-radius: 4px;
				background-color: $white;
				background-clip: padding-box;
				box-shadow: 0px 2px 10px rgba(0, 110, 255, 0.1);
				transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.3s, z-index 0s linear 0.01s;

				& > li {
					display: block;
					padding: 9px 16px;
					text-decoration: none;

					&:first-child {
						margin-top: 10px;
					}

					&:last-child {
						margin-bottom: 15px;
					}

					&:hover {
						background: $color_20;
					}
				}
			}

			&_list_bottom > li {
				@include font_history;
			}

			&_list_top > li {
				@include font_heading;
			}

			&_list_bottom, &_list_top {

				& > li {
					color: $color_1;
				}

				a {
					color: inherit;
					text-decoration: none;
				}
			}

			&_list_top, &_breadcrumb-block {
				display: inline-block;
			}

			&_breadcrumb {
				&-block {
					vertical-align: top;
					margin-left: 4px;
				}

				&-icon, &-current {
					display: inline-block;
				}

				&-icon {
					padding-left: 2px;
					color: $color_1;
					cursor: pointer;

					@include icon(before, arrow-right);

					&::before {
						display: inline-block;
						transition: 200ms linear transform;
					}

					&.active {
						&::before {
							transform: rotate(90deg);
						}
					}
				}

				&-current {
					@include font_heading;

					color: $color_1;
					padding-left: 6px;
				}
			}
		}

		&--right {
			float: right;
			padding-right: 35px;
			margin-top: 30px;

			a {
				color: inherit;
				text-decoration: none;
			}

			#{$l_header_menu}_icon, #{$l_header_menu}_username, #{$l_header_menu}_img {
				display: inline-block;
				vertical-align: middle;
			}

			#{$l_header_menu}_lang {
				display: inline-block;
				vertical-align: baseline;
			}

			#{$l_header_menu}_icon {
				position: relative;
				font-size: 22px;
				color: $black;
				padding-left: 15px;
				padding-right: 10px;

				@include icon(before, gear);

				&:before {
					transition: opacity .2s ease-in-out;
				}

				&:hover {
					.l-header_menu-loginlist {
						display: block;
					}

					&:before {
						opacity: 0.4;
					}
				}

				.l-header_menu-loginlist {
					display: none;
					position: absolute;
					width: 125px;
					top: 30px;
					left: -30px;
					padding: 0;
					text-align: left;
					border-radius: 4px;
					background-color: $white;
					color: $color_1;
					background-clip: padding-box;
					box-shadow: 0px 2px 10px rgba(0, 110, 255, 0.1);

					li {
						list-style: none;
						display: block;
						padding: 9px 16px;
						text-decoration: none;

						@include font_history;

						&:first-child {
							margin-top: 10px;
						}

						&:last-child {
							margin-bottom: 15px;
						}

						&:hover {
							background: $color_20;
						}
					}
				}
			}

			#{$l_header_menu}_username {
				color: $color_1;

				@include font_heading;

				& > span {
					padding: 0 3px;
				}
			}

			#{$l_header_menu}_lang {
				margin-right: 30px;
			}
		}
	}
}

//TO DO: Get rid of it later
.l-header-landing {
	.l-header_menu-loginlist {
		display: block;
	}
}