.b-real-time {
	&-wrap {
		position: relative;
		margin-top: 30px;
		height: 624px;
		background: $color_14;

		.b-real-time {
			&--left {
				padding: 0;
			}
		}
	}

	&--left {
		&_info {
			width: 500px;
			position: absolute;
			left: 90px;
			top: 80px;
			z-index: 5;

			@include desktop-all {
				width: 500px;
			}

			@include desktop-hd {
				width: 600px;
			}
		}

		&_title {
			color: $white;

			@include font_heading_section;
		}

		&_description {
			color: $white;

			@include font_description_main;
		}

		&_visualization {
			position: relative;
			height: 624px;
			margin-right: 15%;
			background: linear-gradient(180.56deg, $color_9 -19.4%, $color_10 102.32%);

			.stars {
				z-index: 1;
				background-image: url("/static/img/static-stars.png");
			}

			.USD {
				z-index: 3;
				background-image: url("/static/img/second-layer.png");
			}

			.BTC {
				z-index: 4;
				background-image: url("/static/img/first-layer.png");
			}

			.EUR {
				z-index: 2;
				background-image: url("/static/img/third-layer.png");
			}

			.stars, .USD, .BTC, .EUR {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: {
					position: 50% 50%;
					repeat: repeat;
					size: 100% auto;
				}
			}
		}
	}

	&--right {
		&_full {
			.b-history {
				&-item {
					padding: 15px;
					padding-bottom: 10px;
					background: $white;
					mix-blend-mode: normal;
					box-shadow: 0px 4px 20px rgba(100, 142, 249, 0.2);
					border-radius: 4px;
					color: $color_9;

					@include font_history;

					&:first-child {
						margin-top: 35px;
					}

					&--Exchange {
						.b-history {
							&-creation {
								width: 35%;
							}

							&_main-info {
								width: 65%;

								.b-service-action {
									margin-right: 15px;

									&-mid {
										@include icon(before, arrow-right);

										&:before {
											font-size: 9px;
											vertical-align: middle;
											padding-top: 2px;
											margin: 0 3px;
										}
									}
								}
							}
						}
					}

					&:hover {
						background: $color_9;
						color: $white;

						.b-service-left-wrapper .b-service-icon {
							.image {
								&.FiatWithdraw, &.CryptoWithdraw {
									@include withdraw_element(30px, 20px, $white, $color_9, $arr-padding: 4, $top: -25px);
								}

								&.Exchange {
									@include exchange_element(34px, 22px, $white, $color_9);
								}

								&.FiatDeposit, &.CryptoDeposit {
									@include deposit_element(34px, 22px, $white, $color_9, $font-size: 22px, $top: -5px, $left: 5px);
								}
							}
						}
					}
				}

				&-creation {
					margin-bottom: 0;
					margin-top: 0;
					width: 40%;
					margin-left: 0;
					text-align: right;
				}

				&_main-user {
					text-align: center;
					margin-top: 10px;
					margin-bottom: 5px;

					.name {
						margin-right: 2px;

						&:after {
							content: ':';
						}
					}

					.value {
						margin-right: 5px;
						margin-left: 2px;
					}
				}

				&_main-info {
					width: 60%;
					margin-bottom: 0;
					margin-top: 0;

					.b-service {
						&-left-wrapper .b-service-icon {
							.image {
								width: 34px;
								height: 22px;
								margin-bottom: 4px;

								&.FiatWithdraw, &.CryptoWithdraw {
									width: 30px;
									height: 20px;
									margin-left: 4px;
									@include withdraw_element(30px, 20px, $color_9, $white, $arr-padding: 4, $top: -25px);
								}

								&.Exchange {
									@include exchange_element(34px, 22px, $color_9, $white);
								}

								&.FiatDeposit, &.CryptoDeposit {
									@include deposit_element(34px, 22px, $color_9, $white, $font-size: 22px, $top: -5px, $left: 5px);
								}
							}
						}

						&-name {
							margin-left: 0;
						}

						&-action {
							margin-right: 40px;

							.currency {
								@include font_history_small;
							}
						}
					}
				}

				&_main-info, &-creation {
					display: inline-block;
					vertical-align: middle;
				}
			}
		}
	}

	&--left, &--right {
		display: inline-block;
		width: 50%;
		height: 100%;
		vertical-align: top;
		padding: 0 20px 20px;
	}
}