.l-accounts {
	position: relative;
	height: calc(100vh - 100px);
	max-width: 30%;
	flex: 1.5 auto;
	background: linear-gradient(0deg, $color_2, $color_2);
	mix-blend-mode: normal;

	& > h2 {
		text-align: left;
		margin: 15px 30px 20px;

		@include font_display;

		color: $white;
	}

	&_filled {
		margin: 30px;
		margin-top: 0;
	}

	.b-account {
		padding: 8px 0 8px 24px;

		&-wrapper {
			position: relative;
			background: $color_3;
			box-shadow: 0 2px 4px rgba(69, 65, 244, 0.1);
			border-radius: 4px;

			&:not(:first-child) {
				margin-top: 24px;
			}

			&--active {
				background: $color_5;
				box-shadow: 0 4px 10px rgba(69, 65, 244, 0.2);

				.b-account {
					padding: 24px 0 8px 24px;

					&-opener {
						background: linear-gradient(90.1deg, $color_6 1.1%, $color_7 99.67%);

						&-relative {
							padding: 0 4px 0;

							&:before {
								transform: rotate(90deg);
							}
						}
					}
				}
			}

			&--BTC {
				.b-account-image {
					background-image: url("/static/img/bitcoin-filled.png");
				}
			}

			&--UAH {
				.b-account-image {
					background-image: url("/static/img/uah-filled.png");
				}
			}
		}

		&-main_info:before {
			content: '';
			height: auto;
			vertical-align: middle;
			display: inline-block;
		}

		&-main_info, &-opener {
			display: inline-block;
		}

		&_main_info {
			width: 90%;
		}

		&-name, &-total {
			display: inline-block;
		}

		&-image, &-text, &-total {
			vertical-align: middle;
		}

		&-image, &-text {
			display: inline-block;
		}

		&-text {
			@include font_body;

			color: $white;
			padding-left: 20px;
		}

		&-total {
			@include font_display;

			color: $white;
			padding-left: 26px;
		}

		&-image {
			width: 36px;
			height: 36px;
			background: {
				repeat: no-repeat;
				size: 36px 36px;
			}
		}

		&-opener {
			position: absolute;
			right: 0;
			top: 0;
			height: 100%;
			background: linear-gradient(90.85deg, $color_7 1.1%, $color_8 99.67%);

			&:hover {
				cursor: pointer;
			}

			&-relative {
				position: relative;
				top: calc(50% - 8px);
				text-align: center;
				padding: 0 14px 0;

				@include icon(before, arrow-right);

				&:before {
					display: inline-block;
					color: $white;
				}
			}
		}

		&-helper-services {
			padding: 0 0 24px 24px;

			.image, .service-url {
				display: inline-block;
			}

			& > div {
				margin-top: 6px;
				margin-left: 12px;

				&:first-child {
					margin-top: 15px;
					margin-bottom: 10px;
				}

				.image {
					height: 18px;
					width: 24px;
				}

				&.deposit {
					.image {
						@include deposit_element(24px, 18px, $white, $color_5, $font-size: 18px, $top: -9px, $left: 2px);
					}
				}

				&.withdraw {
					.image {
						@include withdraw_element(24px, 18px, $white, $color_5);
					}
				}
			}

			.service-url {
				@include font_body;

				color: $white;
				padding-left: 16px;

				a {
					color: inherit;

					&:hover {
						color: inherit;
						text-decoration: none;
					}
				}

				&:hover {
					cursor: pointer;
					text-decoration: underline;
				}
			}
		}
	}

	&_totals {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		padding: 5px 2px;
		background: $color_26;

		& > div {
			&:first-child {
				margin-top: 20px;
			}
		}

		&-title {
			color: $white;
			margin-left: 35px;

			@include font_heading_footer;
		}

		&-value {
			padding-left: 2px;
			color: $white;
			padding-left: 35px;

			@include font_totals;
		}

		&-value, .l-accounts_selectbar {
			display: inline-block;
			width: 50%;
			margin: 10px 0 17px;
		}
	}

	&_selectbar {
		text-align: left;

		@include font_subheading_section;

		& > span {
			margin: 5px;
			padding: 5px;
			color: $color_27;

			&:first-child {
				margin-left: 0;
			}

			&:last-child {
				margin-right: 0;
			}

			&:hover {
				cursor: pointer;
			}

			&.selected {
				color: $white;
			}
		}
	}
}